@-webkit-keyframes scaleBounce {
    0%,
    20%,
    40%,
    60%,
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }

    100% {
        -webkit-transform: scale(1.15);
                transform: scale(1.15);
    }
}

@keyframes scaleBounce {
    0%,
    20%,
    40%,
    60%,
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }

    100% {
        -webkit-transform: scale(1.15);
                transform: scale(1.15);
    }
}

.section.site-map {
    background: $blue;
    -webkit-transition: all 0.45s linear;
    transition: all 0.45s linear;
}

.section.site-map .container {
    position: relative;
    padding: 60px 30px;
}

@media (min-width: $(desktop)) {

    .section.site-map .container {
        padding: 30px 300px 30px 100px;
    }
}

.section.site-map a {
    text-decoration: none;
    display: block;
    width: 100%;
}

.section.site-map .title {
    text-align: center;
    width: 100%;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 3px;
    color: $white;
    font-family: 'Open Sans';
    text-shadow: 1px 1px 2px $black;
    text-transform: uppercase;
    text-decoration: none;
}

@media (min-width: $(desktop)) {

    .section.site-map .title {
        line-height: 0.88;
        text-align: right;
    }
}

.section.site-map .image {
    position: relative;
    background-color: #b1e3ed;
    margin: 0 auto;
    margin-bottom: 15px;
    width: 160px;
    height: 160px;
    border: 3px solid $white;
    border-radius: 50%;
    overflow: hidden;
    -webkit-transition: border-width 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
    transition: border-width 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
    transition: transform 0.4s, border-width 0.2s, box-shadow 0.4s;
    transition: transform 0.4s, border-width 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
}

@media (min-width: $(desktop)) {

    .section.site-map .image {
        z-index: 10;
        position: absolute;
        top: 50%;
        right: 100px;
        margin-top: -80px;
    }
}

.section.site-map .image img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: 0 0;
       object-position: 0 0;
    opacity: 0.75;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    mix-blend-mode: multiply;
    -webkit-transition: all 0.45s;
    transition: all 0.45s;
}

.section.site-map:hover {
    background: $blue_light;
    cursor: pointer;
}

.section.site-map:hover .image {
    -webkit-transition: border-width 0.2s 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
    transition: border-width 0.2s 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
    transition: transform 0.4s, border-width 0.2s 0.2s, box-shadow 0.4s;
    transition: transform 0.4s, border-width 0.2s 0.2s, box-shadow 0.4s, -webkit-transform 0.4s;
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    border-width: 7px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.section.site-map:hover .image img {
    -webkit-transition: all 0.45s;
    transition: all 0.45s;
}
